<template>
	<div id="content-zero">
		<h1>Documentation</h1>
		<div class="column-one">
			<p>In this section you will find all about the game.</p>
			<p>Click or tap the titles below to open them.</p>
		</div>
	</div>
	<ul>
		<li><h1 class="listParent" @click="toggle(0)">Where do i put the songs?</h1></li>
		<div class="content" id="content-one">
			<div class="column-one">
				<p>
					The game comes with a folder called songs, you need to create a folder inside with the name
					of the song (or anything really). Inside that last folder you have to put a song in MP3 format and a background in JPG or PNG format. The background needs to be called "background".
				</p>
			</div>
			<div class="column-two">
				<img id="songFolder" src='../assets/songfolder.png'/>
				<p>The song and the folder could have any name</p>
			</div>
		</div>
		<li><h1 class="listParent" @click="toggle(1)">Song selection menu</h1></li>
		<div class="content" id="content-two">
			<div class="column-one">
				<p>On the right you can select the songs you added to the song folder. You can scroll the list using the mousewheel or the arrow keys.</p>
				<p>You can also search for any song, artist or author on the top search bar.</p>
				<p>On the left is the preview information. it contains useful information about the CPM (characters per minute) through the song.</p>
				<p>Every bar of the graph represents the CPM average of a section of the song. The total average is written on top of the graph.</p>
			</div>
			<div class="column-two">
				<img src='../assets/songselectmenu.jpg'/>
				<p>Use the search bar to filter songs</p>
			</div>
		</div>
		<li><h1 class="listParent" @click="toggle(2)">Editor tabs</h1></li>
		<div class="content" id="content-two">
			<div class="column-one">
				<p>
					The editor screen has 3 tabs, which are used to
					add information about the song, synchronize, and adding subtitles to the song.<br>
				</p>
				<h2>Properties tab</h2>
				<p>There are currently 4 options in this tab. you can input the name of the song, artist, preview time and who created the map.</p>
				<p>The preview time, is the position in which the song will start in the select song menu.</p>
				
				<h2>Input tab</h2>
				<p>
					Here is where you put the lyrics that <strong>you are going to type</strong>, so they must contain any of the 26 letters of the US-Keyboard. It can also contain spaces and apostrophes in between letters, since these will be completed automatically in-game.
				</p>
				<p>
					*You can also put special characters such [ and ], this is explained on a latter section. (See "Fast lyrics" below)
				</p>
				<p>
					If you press the "Insert lyrics" button, you will get a pop-up window where you can paste lyrics. Then they will be automatically separated and put on the timing boxes.
					To time the lyrics, you need to press the "play" button to start the song, then you can add the times of the beginning and finish of every line by using the buttons "Set start", "Set end" or manually. You can also use the keys F9 and F10 instead of the buttons.
				</p>
				<p>
					When the song is playing, a yellow bar will appear over the lyrics for the amount of time you set.
				</p>
				<p>
					Using the minus and plus sign buttons you can add a line below or delete it. Use the "Save" button to save the lyrics. They will be saved to a JSON file inside the song folder.
				</p>
				<h2>Subtitle tab</h2>
				<p>
					Very similar to the previous tab, here you press the "Insert subtitles" button and you will be able to paste the lyrics of the song. These lyrics could be anything you want and they will be updated by every line. If you want a subtitle to be the same for many lines you just paste it many times as needed.
				</p>
			</div>
			<div class="column-two">
				<img src='../assets/properties.jpg'/>
				<p>The save button saves everything!</p>
				<img src='../assets/input.jpg'/>
				<p>Use F9 and F10 to set the start and end time respectively</p>
				<img src='../assets/subtitle.jpg'/>
				<p>Subtitles are optional!</p>
			</div>
		</div>
		<li><h1 class="listParent" @click="toggle(3)">Position edition</h1></li>
		<div class="content" id="content-three">
			<div class="column-one">
				<p>The position screen allows you to move the lyrics horizontally.</p>
				<p>Click and drag the lyrics to wherever you like through the song. You can make the map more readable or make patterns with the words.</p>
				<p>Just like on the input tab, you can seek the song using the player at the top of the screen</p>
			</div>
			<div class="column-two">
				<img src='../assets/position.jpg'/>
				<p>Remember to save after moving the lyrics!</p>
			</div>
		</div>
		<li><h1 class="listParent" @click="toggle(4)">Fast lyrics</h1></li>
		<div class="content" id="content-three">
			<div class="column-one">
				<p>If you think a line of lyrics goes too fast, you can use the special characters " [ " and " ] "</p>
				<p>When you enter the character " [ " the line will turn into a "fast line". Now, the only characters that would be playable would be those inside square brackets</p>
				<p>The " ] " is actually not needed, but it makes it looks better. Technically only the letter on the right of the " [ " will be playable.</p>
				<p>By doing this, when you play the song you will only need to press the characters inside the brackets and the line will autocomplete itself</p>
				<p>Then when you play the song, the characters you put inside the brackets will be highlighted while the rest of the line will have only the outline</p>
			</div>
			<div class="column-two">
				<img src='../assets/fastwords.jpg'/>
				<p>It's better to separate fast lines from normal lines</p>
				<img src='../assets/fastwords2.jpg'/>
				<p>This is how it would look like while playing</p>
			</div>
		</div>
		<li><h1 class="listParent" @click="toggle(5)">Settings menu</h1></li>
		<div class="content" id="content-three">
			<div class="column-one">
				<p>On this menu you can change the window size (you can also press Alt + Enter keys at anytime to change to fullscreen) and the interface language</p>
				<p>You can also play songs with the music player</p>
				<p>If you have problems with the information displayed for any song, try to reload them pressing the "reload songs" button</p>
				<p>On the bottom you can see the version of the game (when it was build) and the link to this website</p>
			</div>
			<div class="column-two">
				<img src='../assets/settings.jpg'/>
				<p>You can access the settings menu from the main screen</p>
			</div>
		</div>
	</ul>
</template>

<script>
	export default {
		name: "TabTwo",
		data() {
			return{
				contentArray: document.getElementsByClassName("content")
			}
		},
		props: {
		},
		methods:{
			toggle(value){
				var content = this.contentArray[value];
				if (content.style.maxHeight == "none"){
					content.style.maxHeight = 0;
				} else {
					content.style.maxHeight = "none";
				}
			}
		},
		components:{
		}
	}
</script>

